import './index.ctc.css';import React from 'react';
import { learnLine } from '../../utils/canvas';
import { initDblTap } from '../../utils/touch';
const defaultPage = 'page_1';
export default class ClassName extends React.Component {
  componentDidMount() {
    this.initPage();
    this.initCanvas(this.canvas);
  }

  initPage() {
    if (!this.page) return;
    this.page.id = `page_${window.$id++}`;
    this.page.style.width = document.documentElement.clientWidth + 'px';
    this.page.style.height = document.documentElement.clientHeight + 'px';
    const pageId = window.location.hash.replace(/#(.*?)$/, '$1') || defaultPage;
    window.$(`#${pageId}`).show();
  }

  initCanvas(canvas) {
    if (!canvas) return;
    canvas.width = document.documentElement.clientWidth;
    canvas.height = document.documentElement.clientHeight;
    learnLine(canvas);
    initDblTap(canvas, event => {
      const width = document.documentElement.clientWidth;
      const clientX = event.touches[0].clientX;
      let next = width / 2 < clientX ? window.$(this.page).next()[0] : window.$(this.page).prev()[0];
      next = next || document.getElementById(defaultPage);
      window.$(this.page).fadeOut(300, () => window.$(next).fadeIn());
    });
  }

  render() {
    const className = this.props.className || '';
    return <div data-type="page" className={`bgc_s_fff d_s_n ${className} `} id={this.props.id} ref={node => this.page = node}>
        <canvas className="p_s_a zi_s_100" ref={node => this.canvas = node} />
        {this.renderHeader()}
        <div className="p_s_a t_s_0 l_s_0 b_s_0 r_s_0 d_s_f fd_s_c ai_s_c jc_s_c p_s_20">
          <div className="mt_s_-120 d_s_f fd_s_c ai_s_c">{this.props.children}</div>
        </div>
        {this.renderFooter()}
      </div>;
  }

  renderHeader() {
    return <img className="h_s_25 ctc_m_s_10_ml_s_8" src="./logo.jpg" alt="" />;
  }

  renderFooter() {
    return <img className="h_s_80 m_s_10 p_s_a b_s_0 r_s_0" src="./me.jpeg" alt="" />;
  }

}